ight:320px}.paint-wrapper .paint-title .title{text-align:center;color:#b7e5fc;transition:color 0.2s ease;letter-spacing:1px;font-family:"Kanit", sans-serif;font-family:"Kanit", sans-serif}.paint-wrapper .paint-title:hover{background-color:rgba(0,0,0,0.5)}.paint-wrapper .paint-title:hover .title{color:#72d1fd}.congratulations{color:#0b6b98;font-family:"Kanit", sans-serif;font-size:1.2rem}

